html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem;
}

[class*=contenedor] {
  max-width: 120rem;
  width: 95%;
  margin-inline: auto;
}

h1, h2, h3 {
  margin: 0 0 4rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

a {
  text-decoration: none;
}

img {
  width: 100%;
  display: block;
}

ul {
  padding: 0;
  list-style: none;
  margin: 0;
}

.header {
  background-color: #4cb8b3;
  padding: 3rem 0;
  text-align: center;
  box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.8);
}
@media (min-width: 768px) {
  .header {
    padding: 1.5rem 0;
  }
}
@media (min-width: 768px) {
  .header__grid {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.header__titulo {
  margin: 0 0 4rem 0;
  color: #ffffff;
}
@media (min-width: 768px) {
  .header__titulo {
    margin: 0;
  }
}
.header--active {
  position: fixed;
  width: 100%;
}

.navegacion {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
}
@media (min-width: 768px) {
  .navegacion {
    flex-direction: row;
  }
}
.navegacion__enlace {
  color: #ffffff;
  text-decoration: none;
  font-size: 2rem;
}
.navegacion__enlace:hover {
  color: #fef46d;
}

.video {
  min-height: 58rem;
  max-height: 60rem;
  overflow: hidden;
  position: relative;
}
@media (min-width: 768px) {
  .video {
    min-height: 68rem;
    max-height: 70rem;
  }
}
.video__overlay {
  position: absolute;
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
}
.video__overlay::after {
  content: "";
  position: absolute;
  background: linear-gradient(270deg, rgba(126, 2, 195, 0.3) 0%, rgba(255, 0, 110, 0.3) 100%);
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}
.video__contenido {
  z-index: 10;
  display: grid;
  grid-template-columns: 1.5fr 1fr;
}
@media (min-width: 768px) {
  .video__contenido {
    grid-template-columns: 2fr 1fr;
  }
}
.video__titulo {
  line-height: 1.5;
  color: #ffffff;
  font-size: 5rem;
  margin: 0;
}
@media (min-width: 768px) {
  .video__titulo {
    font-size: 7rem;
    margin-bottom: 1rem;
  }
}
.video__grid {
  grid-column: 1/3;
}
@media (min-width: 480px) {
  .video__grid {
    grid-column: 1/2;
  }
}
.video__meta {
  background-color: #f53756;
  display: block;
  color: #ffffff;
  padding: 1rem 2rem;
  line-height: 1.5;
  font-weight: 700;
  font-size: 2.4rem;
  margin: 0;
  display: inline;
}
.video__video {
  width: auto;
  position: absolute;
}

.sobre-festival {
  padding-block: 5rem;
}
@media (min-width: 768px) {
  .sobre-festival__grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    place-items: center;
    column-gap: 4rem;
  }
}
.sobre-festival__titulo {
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .sobre-festival__titulo {
    margin: 0 0 4rem 0;
  }
}
.sobre-festival__meta {
  color: #f53756;
  font-size: 2rem;
  font-weight: bold;
  margin-block: 3rem;
}
.sobre-festival__texto {
  margin-block: 2rem;
  font-size: 2rem;
}

.line-up {
  background-color: #f53756;
  padding-block: 5rem;
}
.line-up__titulo {
  color: #ffffff;
  text-align: center;
}
.line-up__dia {
  color: #ffffff;
  text-align: center;
  font-size: 2.6rem;
  text-transform: uppercase;
  margin-block: 5rem;
}
@media (min-width: 768px) {
  .line-up__grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 4rem;
  }
}

.escenario {
  padding-top: 3rem;
}
.escenario--amarillo {
  background-color: #fef46d;
}
.escenario--verde {
  background-color: #4cb8b3;
}
.escenario__titulo {
  font-size: 2rem;
  text-align: center;
  margin-bottom: 2rem;
}
.escenario__icono {
  width: 15rem;
  margin: 3rem auto;
}
.escenario__li {
  padding: 2rem;
}
.escenario__li--amarillo:nth-child(odd) {
  background-color: #fdee21;
}
.escenario__li--verde:nth-child(odd) {
  background-color: #348380;
}
.escenario__li--span {
  font-weight: bold;
}

.galeria {
  padding: 4rem 0;
}
.galeria__titulo {
  text-align: center;
}
.galeria__lista {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
}
@media (min-width: 480px) {
  .galeria__lista {
    grid-template-columns: repeat(3, 1fr);
  }
}
@media (min-width: 768px) {
  .galeria__lista {
    grid-template-columns: repeat(4, 1fr);
  }
}
@media (min-width: 992px) {
  .galeria__lista {
    grid-template-columns: repeat(6, 1fr);
  }
}
.galeria__imagen {
  cursor: pointer;
}

.boletos {
  background-color: #4cb8b3;
  padding: 4rem 0;
}
.boletos__titulo {
  color: #ffffff;
  text-align: center;
}
.boletos__grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  column-gap: 5rem;
}

.pase__titulo {
  color: #ffffff;
  font-size: 3rem;
  text-align: center;
}
.pase__heading {
  color: #ffffff;
  font-size: 2.4rem;
  background-color: #f53756;
  text-transform: uppercase;
  text-align: center;
  margin: 0;
  padding: 0.5rem;
}
.pase__li {
  background-color: #ffffff;
  padding: 0.5rem;
  font-size: 2rem;
  border-bottom: 2px solid rgba(0, 0, 0, 0.1);
}
.pase__li:last-of-type {
  border-bottom: none;
}
.pase__footer {
  margin: 0;
  color: #ffffff;
  background-color: purple;
  opacity: 0.7;
  padding: 0.5rem;
  text-align: center;
  font-weight: bold;
  font-size: 3rem;
}

.overlay {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 4rem;
  background-color: rgba(0, 0, 0, 0.8);
}
.overlay__div-imagen {
  position: relative;
}
.overlay__imagen {
  width: 90%;
  margin-inline: auto;
}
.overlay__eliminar {
  background-color: #f53756;
  width: 4rem;
  height: 4rem;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #ffffff;
  font-weight: bold;
  font-size: 2rem;
  position: absolute;
  top: -3.5rem;
  right: 0.5rem;
  transition-property: opacity;
  transition-duration: 300ms;
  transition-timing-function: ease;
  cursor: pointer;
}
@media (min-width: 768px) {
  .overlay__eliminar {
    top: -4.5rem;
  }
}
.overlay__eliminar:hover {
  background-color: rgba(245, 55, 86, 0.6);
}

.fijo {
  overflow: hidden;
}

.footer {
  background-color: #f53756;
}
.footer__copyright {
  margin: 0;
  padding: 2rem;
  text-align: center;
  color: #ffffff;
  font-size: 2rem;
}